<script>
  import Router from './../router';
  import Tool from '../configs/tool.js';
  import axios from  'axios';
  import { api } from '../../static/dicConfig';

  export default {
    name: 'DetailFootBtn',
    props:{
      currentobj: {
        type:Object,
        default(){
          return {
            roomid:-1,
            postid:-1,
            state:0,
            campusid:-1
          }
        }
        }
      },
    data(){
      return {
        default: true
      }
    },
    methods: {
     setScore(e,stated){
       if(!this.currentobj||!this.currentobj.campusid)
       {
          return false;
       }
        //设置成绩
        let actionurl='';
        switch (stated) {
          case 1:
            actionurl=`${api.host}/ResumeOption/setPass`;
            break;
          case 2:
            actionurl=`${api.host}/ResumeOption/setNoPass`;
            break;
          case 3:
            actionurl='';
            break;
        }
        if(stated!==this.currentobj.state){
            axios.get(actionurl,
            {
               params: {RoomId: this.currentobj.roomid , PostId:this.currentobj.postid,CampusId:this.currentobj.campusid}
            }).then((res) => {
                  if (res.status === 200) {
                    if (res.data.returncode === 0) {
                       if(res.data.result){
                         mui.toast('操作成功');
                       }
                      }else
                      {
                        if(res.data.message){
                          mui.toast(res.data.message);
                        }
                      }
                  }
            })
        }
     },
     showcomment(e)
     {
         //切换路由到评论页
        this.$router.push({
          name: 'ResumeComment',
          query: {
            state: this.currentobj.state,
            campusId:this.currentobj.campusid,
            roomId:this.currentobj.roomid,
            postId:this.currentobj.postid,
          }
        });
     }
    },
    created(){
    }
  }
</script>

<template>
  <footer class="btn_footer"  >
     <ul>
         <li @click="setScore($event,1)">通过</li>
         <li @click="setScore($event,2)">不通过</li>
         <li @click="showcomment($event)"><span>添加评语</span></li>
     </ul>
  </footer>
</template>
<style lang="less" scoped>
 @import "../assets/css/detailFootBtn.less";
</style>


