<template>
    <div>
      <common-header :title="titlename"></common-header> 
      <div class="laout_fdl" @click="detailLeft($event)">左</div> 
      <div class="laout_fdr" @click="detailRight($event)">右</div>
      <div class="content" v-show="this.resumedata">
          <div class="topinfo" v-if="this.resumedata.resume">
              <dl>
                  <dt v-if="this.doMainUlr&&this.resumedata.resume.photo_attach.work_photo_path.Text
                  &&this.resumedata.resume.photo_attach.work_photo_path.Text!=='null'"><img :src="this.doMainUlr+this.resumedata.resume.photo_attach.work_photo_path.Text" /></dt>
                  <dt v-else><img src="../assets/images/defaultPhoto.png"/></dt>
                  <dd class="urseName">
                    <span v-if="this.resumedata.resume.person_info">{{this.resumedata.resume.person_info.name.Text}}</span>
                  </dd>
                  <dd >
                    <span v-if="this.resumedata.resume.person_info"><img v-show="this.resumedata.resume.person_info.gender.Text==='女'" src="../assets/images/male.png"  /><img v-show="this.resumedata.resume.person_info.gender.Text==='男'" src="../assets/images/male.png" />{{this.resumedata.resume.person_info.gender.Text}}</span>
                    <span v-if="this.resumedata.resume.education_experience[0]"><img src="../assets/images/edu.png" />{{this.resumedata.resume.education_experience[0].education_degree.Text}}</span>
                    <span v-if="this.resumedata.resume.education_experience[0]"><img src="../assets/images/school.png" />{{this.resumedata.resume.education_experience[0].edu_type.Text}}</span></dd>
                  <dd>
                    <span v-if="this.resumedata.resume.education_experience[0]">{{this.resumedata.resume.education_experience[0].school_name.Text}}</span>
                    <span v-show="this.resumedata.resume.education_experience[0].school_name.Text&&this.resumedata.resume.education_experience[0].major_name.Text">|</span>
                    <span v-if="this.resumedata.resume.education_experience[0]">{{this.resumedata.resume.education_experience[0].major_name.Text}}</span>
                  </dd>
              </dl>
          </div>
          <div class="examinfo" v-if="this.examdata">
                <div class="fdiv"><span class="defaultspan">考场安排</span></div>
                <div class="divcon">
                    <div v-show="this.examdata.Position">投递岗位：<span class="black">{{this.examdata.Position}}</span></div>
                    <div v-show="this.examdata.Address">所在考场：<span class="black">{{this.examdata.Address}}   <span v-show="this.examdata.SeatNo" class="black">{{this.examdata.SeatNo}} 号</span></span></div>
                    <div v-show="this.examdata.StartDate">面试时间：<span class="black">{{this.examdata.StartDate}}</span></div>
                </div>
          </div>
           <div class="resumeinfo"  v-if="checkisnull(this.resumeConfig)&&this.resumedata.resume">
            <div v-bind:key="index" v-for="(item,index) in this.resumeConfig">
                <div class="fdiv" v-if="item.caption&&checkmodelisnull(resumedata.resume[item.field_name],item.item)"><span v-if="item.caption==='个人信息'" class="defaultspan">学生简历</span><span v-else>{{item.caption}}</span></div>
                <div class="divcon" v-bind:key="key" v-for="(obj,key) in item.item" v-if="isshowrow(obj,resumedata.resume[item.field_name][obj.field_name])&&!Array.isArray(resumedata.resume[item.field_name])" >
                     <div v-if="Array.isArray(resumedata.resume[item.field_name][obj.field_name])">
                          {{obj.caption}}：
                        <ul >
                          <li v-bind:key="indexs" v-for="(subItem,indexs) in resumedata.resume[item.field_name][obj.field_name]">
                            <span v-bind:key="k" v-for="(keys,k,i) in subItem" v-if="keys&&keys.Value" class="black"> 
                              {{getDealWithField(subItem[keys])}}<span v-show="islastarray(keys,i,subItem)">|</span>
                            </span>
                          </li>
                        </ul>
                     </div>
                     <div v-else-if="!Array.isArray(resumedata.resume[item.field_name][obj.field_name])&&obj.caption">
                         <div v-if="item.caption!=='附件'"><span>{{obj.caption}}：</span><span class="black">{{getDealWithField(resumedata.resume[item.field_name][obj.field_name])}}</span></div>
                         <div v-else>
                           <div v-if="obj.caption.indexOf('照')>-1">
                           <div class="zhaopin_p">{{obj.caption}}：</div>
                           <div class="last_img"><img :src="doMainUlr+getDealWithField(resumedata.resume[item.field_name][obj.field_name])"/></div>
                           </div>
                           <div v-else-if="obj.caption.indexOf('附件')>-1">
                             <div class="zhaopin_p">{{obj.caption}}</div>
                             <div class="a_link"><a :href="doMainUlr+getDealWithField(resumedata.resume[item.field_name][obj.field_name])" target="_blank">{{getDealWithField(resumedata.resume[item.field_name][obj.field_name])}}</a></div>
                           </div>
                           <div v-else>
                             <div>{{obj.caption+"："+getDealWithField(resumedata.resume[item.field_name][obj.field_name])}}</div>
                           </div>
                         </div>
                     </div>
                </div>
                <div v-if="resumedata.resume[item.field_name]&&Array.isArray(resumedata.resume[item.field_name])">
                  <div class="divcon clearfix" v-bind:key="ox" v-for="(objex,ox) in resumedata.resume[item.field_name]" v-if="item.item[0]&&objex[item.item[0].field_name]&&checkarrayisnull(objex)">
                    <div v-if="item.field_name==='education_experience'">
                      <div v-bind:key="oe" v-for="(obje,oe) in item.item" v-if="objex[obje.field_name]&&obje.caption&&objex[obje.field_name]&&objex[obje.field_name].Text">
                       <div class="c_padding_bottom clearfix" v-if="obje.field_name=='school_name'||obje.field_name=='project_name'||obje.field_name=='company_name'">
                        <p class="fleft black schoolName">
                           {{ getDealWithField(objex[obje.field_name]) }}
                        </p>
                        <div class="fright">
                          <span v-if="objex['edu_date_start']">
                            {{ replacestr(getDealWithField(objex['edu_date_start']),'-','.').substr(0,7) }} - {{ replacestr(getDealWithField(objex['edu_date_end']),'-','.').substr(0,7) }}
                          </span>
                          <span v-if="objex['project_date_start']">
                            {{ replacestr(getDealWithField(objex['project_date_start']),'-','.').substr(0,7) }} - {{ replacestr(getDealWithField(objex['project_date_end']),'-','.').substr(0,7) }}
                          </span>
                          <span v-if="objex['job_date_start']">
                            {{ replacestr(getDealWithField(objex['job_date_start']),'-','.').substr(0,7) }} - {{ replacestr(getDealWithField(objex['job_date_end']),'-','.').substr(0,7) }}
                          </span>
                        </div>
                       </div>
                       <div>
                        <div class="c_padding clearfix" v-if="obje.caption==='学历'" >
                          {{ getDealWithField(objex['education_degree']) }}<span v-show="!!getDealWithField(objex['major_name'])"> | </span>{{ getDealWithField(objex['major_name'])}}
                        </div> 
                       </div>
                       <div  class="c_padding" v-if="!specialFieldList[obje.field_name]&&!isNull(obje.caption)&&obje.caption.indexOf('毕业时间')<0&&obje.caption.indexOf('入学时间')<0">
                        {{obje.caption}}：<span class="black">{{getDealWithField(objex[obje.field_name])}}</span>
                      </div>
                      </div>
                    </div>
                     <div class="family-relation" v-else-if="obje.field_name==='family_name'">
                      <span v-if="obje.field_name==='family_name'">
                        {{ getDealWithField(objex[obje.field_name]) }}<span v-show="!!getDealWithField(objex[obje.field_name])&&!!getDealWithField(objex['family_relation'])"> | </span>{{getDealWithField(objex['family_relation'])}}
                      </span>
                      <span v-if="obje.field_name=='family_company'" class="company-work">
                        {{ getDealWithField(objex[obje.field_name]) }}<span v-show="!!getDealWithField(objex[obje.field_name])&&!!getDealWithField(objex['family_position'])"> | </span>{{getDealWithField(objex['family_position'])}}
                      </span>
                    </div>
                    <div v-else>
                      <div class="for_div" v-bind:key="oe" v-for="(obje,oe) in item.item" v-if="objex[obje.field_name]&&obje.caption&&objex[obje.field_name]&&objex[obje.field_name].Text">
                        <div>
                         {{obje.caption}}：<span class="black">{{getDealWithField(objex[obje.field_name])}}</span>
                         </div>
                     </div>
                    </div>
                     <div class="bottom_border clearfix" v-show="ox!==resumedata.resume[item.field_name].length-1"></div>
                  </div>
                </div>
            </div>
          </div>
      </div> 
      <btn-footer :currentobj="this.currentobj"></btn-footer> 
    </div>
</template>
<script>
import Header from './common/Header.vue';
//import Footer from './common/Footer.vue';
import Footer from './DetailFootBtn.vue';
import axios from 'axios';
import {mapGetters, mapActions} from 'vuex';
import { doMain,api } from '../../static/dicConfig';

export default {
  name:'ResumeDetails',
  components:{
     'common-header': Header,
     'btn-footer': Footer,
  },
  methods:{
    ...mapActions([
        'getResumeInfoData'
    ]),
    initResumeData(data)
    {
        if(!!data&&data.returncode==='0')
        {
            this.resumedata=!!data.result.ResumeSnapshot?JSON.parse(data.result.ResumeSnapshot):'';
            this.examdata=!!data.result.ExamInfo?data.result.ExamInfo:'';
            this.currentstate=!!data.result.SiftStatus?data.result.SiftStatus:'0';
            this.currentobj={
                roomid:this.$route.query.roomId,
                postid:this.$route.query.postID,
                state:this.currentstate,
                campusid:this.$route.query.campusId
            }
            if(this.resumedata.resume)
            {
              localStorage.setItem("resumeinfo",JSON.stringify(this.resumedata.resume));
            }
        }
    },
    checkisnull(data)
    {
       if(data===null||data===undefined||data==='')
       {
         return false;
       }
       return true;
    },
    //替換指定字符
    replacestr(str,kstr,tstr)
    {
        if(!!str)
        {
          return str.replace(new RegExp('['+kstr+']','g'),tstr);
        }
        return '';
    },
    //檢查當前模块 是否有值 
    checkmodelisnull(currentmodel,configdata)
    {
        let bclist=[];
        if(!Array.isArray(currentmodel)){
          for(let obj of configdata){
                if(!Array.isArray(currentmodel[obj.field_name])&&obj.caption){
                      if(!!currentmodel[obj.field_name]&&!!currentmodel[obj.field_name].Text)
                      {
                          bclist.push(true);
                      }else{
                          bclist.push(false);
                      }
                     
                }else if(Array.isArray(currentmodel[obj.field_name])&&obj.caption){
                     for(let o of currentmodel[obj.field_name]){
                        for(let key in o){
                          if(!!o[key]&&!!o[key].Text)
                          {
                              bclist.push(true);
                          }else{
                            bclist.push(false);
                          }
                        }
                     }
                }else{
                  bclist.push(false);
                }
          }
        }else{
          for(let objex of currentmodel){
            for(let obje of configdata){
              if(!!objex[obje.field_name]&&!!objex[obje.field_name].Text)
              {
                  bclist.push(true);
              }else{
                  bclist.push(false);
              }
            }
          }
        }
        if(bclist.indexOf(true)>-1)
        {
          return true;
        }
        return false;
    },
    //檢查當前array 是否有值  
    checkarrayisnull(currentarray)
    {
        let bclist=[];
        for(let items in currentarray){
            if(!!currentarray[items]){
              if(currentarray[items].Text==null||currentarray[items].Text=='null'||!currentarray[items].Text)
              {
                  bclist.push(false);
              }else{
                  bclist.push(true);
              }
            }
          }
        if(bclist.indexOf(true)>-1)
        {
          return true;
        }
        return false;
    },
    isshowrow(currentobj,currentrow)
    {
        if(currentobj.caption&&currentobj.field_name!=='name'&&currentobj.field_name!=='gender'&&!!currentrow&&!!currentrow.Text)
        {
          return true
        }
        return false;
    },
    getAges(str) {
      let r = str.match(/^(\d{1,4})(-|\/)(\d{1,2})\2(\d{1,2})$/);
      if (r == null) return '';
      let d = new Date(r[1], r[3] - 1, r[4]);
      let Y;
      if (d.getFullYear() == r[1] && (d.getMonth() + 1) == r[3] && d.getDate() == r[4]) {
        Y = new Date().getFullYear();
      }
      return (Y - r[1]) + '岁';
    },
    isNull(str) {
      if (!str) return true;
      var regu = "^[ ]+$";
      var re = new RegExp(regu);
      return re.test(str);
    },
     getDealWithField(obj) {
      obj = obj || {};
      return this.isNull(obj.Text) ? '' : obj.Text;
    },
    detailLeft(e){

    },
    detailRight(e)
    {

    },
    //如果为最后一个值则返回false
    islastarray(key,i,objlist)
    {
      let index=0;
      for(let item in objlist)
      {
        if(!!objlist[item].Text){
          index++;
        }
      }
      if(index===(i+1))
      {
        return false;
      }
      return true;
    },
    comparesortitem(obj1,obj2){
       return obj1.i-obj2.i;
   }
  },
  data () {
    return {
      titlename: '简历详情页面',
      currentstate:0,
      currentobj:{},
      resumedata:'',
      examdata:'',
      resumeConfig:[],
      doMainUlr:'',
      defaultUrl:'',
      specialFieldList: {
        school_name: '1',
        company_name: '1',
        //college_name: '1',
        project_name: '1',
        edu_date_start: '1',
        project_date_start: '1',
        job_date_start: '1',
        edu_date_end: '',
        project_date_end: '1',
        job_date_end: '1',
        education_degree: '1',
        responsibility: '1',
        family_position: '1',
        major_name: '1',
        project_title: '1',
        job_type: '1',
        job_title: '1',
        family_name: '1',
        family_relation: '1',
        family_company: '1',
        family_position: '1'
      },
    }
  },
  computed: {
    ...mapGetters({
      resumeInfo: 'resumeInfo'
    })
  },
    created(){
      this.doMainUlr=doMain.url;
      axios.get(`${api.host}/ResumeOption/GetCompanyTempAllMoudle`,
      {
         params: {companyId: this.$route.query.campusId , displayAirmanFields: false}
      }).then((res) => {
            if (res.status === 200) {
              let  dataresult=JSON.parse(res.data);
               if (dataresult.returncode ===0) {
                    let configList=dataresult.result.custom_info.Moudle;
                    console.log(configList);
                    if(configList)
                    {
                      let options=[];
                      for(let key of configList){
                         
                          if(key&&key.field_name==='education_experience')
                          {
                             let resetitem=[];
                             let sortdata=[];
                            for(let items in key.item)
                            {
                                switch(key.item[items].field_name)
                                {
                                    case 'school_name':
                                     resetitem.push({k:key.item[items],i:0});
                                    break;
                                    case 'edu_date_start':
                                      resetitem.push({k:key.item[items],i:1});
                                    break;
                                    case 'edu_date_end':
                                      resetitem.push({k:key.item[items],i:2});
                                    break;
                                    case 'education_degree':
                                      resetitem.push({k:key.item[items],i:3});
                                    break;
                                    case 'edu_type':
                                      resetitem.push({k:key.item[items],i:4});
                                    break;
                                    default:
                                      resetitem.push({k:key.item[items],i:5});
                                    break;
                                }
                            }
                            for(let items of resetitem)
                            {
                                sortdata.push(items.k);
                            }
                            if(sortdata){
                              key.item=sortdata;
                            }
                          }
                          options.push(key);
                      }
                      if(options){
                          this.resumeConfig=options;
                      }
                    }       
                }
            }
      })
      this.getResumeInfoData({
        campusId: this.$route.query.campusId,
        postID:this.$route.query.postID,
        roomId:this.$route.query.roomId,
        callback:()=>{
          //初始化简历数据
          this.initResumeData(this.resumeInfo);
      }
      });
    }
}
</script>

<style lang="less" scoped>
 @import "../assets/css/resumeDetails.less";
</style>


